<template>
  <div class="login-box">
    <img src="../../assets/images/logo.png" class="logo-img" />

    <div style="padding-right: 2.036rem;">
      <van-field v-model="tel" class="w-100b" placeholder="请输入手机号">
        <img
          class="icon-img"
          style="width: 1.071rem;height: 1.643rem;"
          slot="label"
          src="../../assets/images/account/phone.png"
        />
      </van-field>

      <van-field
        v-model="password"
        class="w-100b"
        type="password"
        style="margin-top: 2.464rem;"
        placeholder="请输入密码"
      >
        <img
          class="icon-img"
          style="width: 1.214rem;height: 1.5rem;"
          slot="label"
          src="../../assets/images/account/password.png"
        />
      </van-field>

      <div class="tx-right" style="margin-top: 0.536rem;">
        <span class="font-9 color-2D8BFF">忘记密码？</span>
      </div>
    </div>

    <van-button
      class="submit-bt"
      @click="onSubmit"
      style="border-radius: 0.643rem;margin-top: 2.929rem;"
      color="#FEA503"
    >登录</van-button>
    <div class="tx-right" style="margin-top: 1.321rem;" v-if="type==1">
      <router-link to="/register">
        <span class="font-9 color-2D8BFF" style="margin-right: 1.179rem;">没有账号，立即去注册</span>
      </router-link>
    </div>

    <div v-if="type==1" class="color-989898 tx-center" style="margin-top: 5rem;">
      <img src="../../assets/images/account/wx-logo.png" style="width: 2.214rem;height: 1.857rem;" />
      <p class="font-12" style="margin-top: 0.857rem;">微信快捷登陆</p>
    </div>

    <van-dialog v-model="show" title="请选择进入端" style="text-align: center;line-height: 50px;">
      <div
        @click="user_type(item.types)"
        :class="item.types===type?'aaa':''"
        v-for="(item,index) in comes"
        :key="index"
      >{{item.text}}</div>
    </van-dialog>
  </div>
</template>

<script>
import { login, herdsmanLogin } from "../../api/api";
export default {
  name: "login",
  data() {
    return {
      type: 1,
      show: true,
      tel: "",
      password: "",
      comes: [
        {
          text: "用户",
          types: 1
        },
        {
          text: "牧民",
          types: 2
        },
        {
          text: "屠宰",
          types: 3
        }
      ]
    };
  },
  methods: {
    /**
     * @description: 提交数据
     * @date 2020/2/12
     */

    user_type(index) {
      console.log(111);
      this.type = index;
      console.log(this.type);
    },
    onSubmit() {
      localStorage.setItem("type", this.type);
      let data = {
        tel: this.tel,
        password: this.password
      };
      if (this.type == 1) {
        login(data).then(res => {
          console.log(res);
          if (res.data.status == 200) {
            localStorage.setItem("id", res.data.data.id);
            localStorage.setItem("access_token", res.data.data.token);
            this.$router.push({
              path: "/home"
            });
          } else {
            alert(res.data.msg);
          }
        });
      } else if (this.type == 2) {
        console.log(2);
        herdsmanLogin(data).then(res => {
          console.log(res);
          if (res.data.status == 200) {
            localStorage.setItem("id", res.data.data.id);
            localStorage.setItem("access_token", res.data.data.token);
            this.$router.push({
              path: "/nomads-home"
            });
          } else {
            alert(res.data.msg);
          }
        });
      }
    }
  },
  created() {
    this.istype = localStorage.getItem("type");
  }
};
</script>

<style scoped lang="less">
/deep/ .van-field__label {
  width: auto;
  padding-right: 1.143rem;
}

.login-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 3.143rem;

  /*Logo*/

  .logo-img {
    width: 6.571rem;
    height: 4.571rem;
    margin: 5.071rem auto 4.286rem;
  }
  .aaa {
    background-color: rgb(254, 165, 3);
    color: #ffffff;
  }
}
</style>
